<template>
  <div>
    <b-dropdown id="dropdown-main" class="mt-2" variant="outline" style="background:white;height:40px;width:80px;padding:0px">
      <span slot="button-content" style="padding:0px">
        <div :style="menuStyle(currentSize)" />
      </span>
      <b-dropdown-item @click="updateValue(i)" v-for="i in currentSizeList" :key="i">
        <div :style="menuStyle(i)" />
      </b-dropdown-item>
    </b-dropdown>
  </div>    
</template>

<script>
export default {
  name: "lineSizeMenu",
  props: ['sizeList', 'color', 'defaultSize'],
  data() {
    return {
      currentSize: 0,
      currentSizeList: [3, 5, 7, 9, 11, 13, 15],
      currentColor: "black"
    };
  },
  mounted() {
    if (this.color) this.currentColor = this.color;
    if (this.sizeList) this.currentSizeList = this.sizeList;
    if (this.defaultSize) this.currentSize = this.defaultSize;

    if (this.currentSize == 0) {
      this.currentSize = this.currentSizeList[0];
    }
  },  
  methods: {
    menuStyle (size) {
      if (this.color) this.currentColor = this.color;
      return "background:"+ this.currentColor + ";width:70px;height:" + size + "px;"
    },
    
    updateValue: function (value) {
      this.currentSize = value;
      //console.log("hello " + value);
      this.$emit('input', value);
    }
  }  
};
</script>
