<template>
  <div id="main-content" class="main">
    <div class="row" style="max-height: 30%; overflow-y: auto">
      <div class="col-md-12">
        <h3 align="center">Welcome to Jack's Drawing Game</h3>
        <form>
          <div class="form-group">
            <b-row>
              <b-col cols="1">
                <label for="name" style="display:block;text-align:right">Name:</label>
              </b-col>
              <b-col cols="4">
                <b-input
                  type="text"
                  id="name"
                  size="sm"
                  class="form-control ml-0"
                  v-model="name"
                  :state="nameState"
                  placeholder="Please enter your name here..."
                />
              
                <b-form-invalid-feedback id="name-feedback">
                  Enter at least 3 and at most 6 letters
                </b-form-invalid-feedback>
              </b-col>
              <b-col cols="3" xl="2">
                <b-button
                  block
                  id="connect"
                  size="sm"
                  variant="outline-primary"
                  class="badge-default ml-1 mr-1"
                  type="submit"
                  :disabled="connected == true || nameState == false"
                  @click.prevent="connect"
                  >Join</b-button
                >
              </b-col>
              <b-col cols="3" xl="2">
                <b-button
                  block
                  id="disconnect"
                  variant="outline-primary"
                  size="sm"
                  class="badge-default ml-1 mr-1"
                  type="submit"
                  :disabled="connected == false"
                  @click.prevent="disconnect"
                >
                  Leave
                </b-button>
              </b-col>
            </b-row>
          </div>
        </form>
      </div>
    </div>

    <div>
      <h5>
          Color Guide
          <span style="color:green" v-if="connected">Connected</span>
          <span style="color:red" v-if="!connected">Disconnected</span>
      </h5>
      <b-row>
        
        <b-col cols="2">
          <div style="display:flex"> 
          <colorPicker v-model="strokeStyle" :disabled="!connected" v-on:change="selectColor" class="color" 
            v-b-tooltip title="Select custom color" />
          <lineSizeMenu v-model="lineSize" :color="strokeStyle" :defaultSize="lineSize" />
          </div>
        </b-col>

        <b-col
          cols="1"
          class="color ml-2"
          v-for="(x, i) in colorList"
          :key="i"
          :style="getColorStyle(x)"
          @click="selectColor(x)"
        >
        </b-col>
        <b-col cols="1" class="ml-0">
          <b-img src="eraser-big.png" class="mt-2 mb-2" v-b-tooltip title="Enjoy my eraser, $1/minute" style="display:block;margin:auto;"
            :style="getColorStyle(background)" @click="selectColor(background)"/>
        </b-col>
        <b-col cols="3" style="padding-left:0">
          <b-button size="sm" variant="primary" class="color mt-1" @click="clearCanvas()" :disabled="!connected || canvasDisabled()">Clear</b-button>
          <b-button size="sm" variant="primary" class="color mt-1 ml-2" @click="startGame()" :disabled="disableStart()">Start Game</b-button>
        </b-col>

      </b-row>
    </div>
    <div>
      <b-row>
        <b-col cols="2" style="border-style:solid;border-width:1px;padding:0px">
          <b-list-group style="max-width: 300px;">
            <b-list-group-item class="d-flex align-items-center mt-0" style="padding-left:10px;padding-right:10px" v-for="(u, i) in sortedUsers" :key="i">
              <b-avatar variant="primary" class="mr-2" size="md" :text="u.name.substring(0,2)" :src="u.ghost ? 'ghost.png' : u.icon"></b-avatar>
              <span :class="getUserClass(u)">{{u.name}}</span>
              <b-badge>{{u.score}}</b-badge>
            </b-list-group-item>
          </b-list-group>
        </b-col>

        <b-col cols="7" xl="6" style="overflow: auto">
          <b-row class="mb-1">
            <b-col><span style="font-size:14px"><b>Round: {{gameStatus.currentRound}} of {{gameStatus.totalRounds}}</b></span></b-col>
            <b-col cols="5"><center><b><span style="font-size:20px;white-space:pre;">{{gameStatus.drawingWordMask.split('').join(' ')}}</span></b></center></b-col>
            <b-col><span style="font-size:14px;float:right;"><b>Time Left: <span style="color:red">{{gameStatus.timeRemaining}} secs</span></b></span></b-col>
          </b-row>
          <b-row>
            <b-col >
              <!-- Bind the custom directive to the canvas element -->
              <canvas ref="canvas" v-canvas></canvas>
            </b-col>
          </b-row>
        </b-col>

        <b-col cols="3" style="margin:0;padding:5px">
          <div class="row chatroom" v-chat-scroll>
            <ul class="message">
              <li class="message" v-for="(item, index) in received_messages" :key="index"><span v-html="item" /></li>
            </ul>
          </div>
          <div style="height: 10%;width:230px">
            <input type="text" id="message" 
                  ref="message"
                  class="form-control"
                  v-model="send_message"
                  :disabled="connected == false"
                  placeholder="Please enter your message here..."
                  @keyup="sendMsg"
                />
          </div>
        </b-col>
      </b-row>
    </div>

    <b-modal ref="my-modal" :title="'Choose your word - ' + timeLeftForChooseWord" size="sm" 
      centered hide-footer no-close-on-esc no-close-on-backdrop hide-header-close>
      <b-button class="mt-2" variant="outline-primary" block v-for="(x, i) in wordList" :key="i" @click="chooseWord(x)">{{x}}</b-button>
      <b-button class="mt-2" variant="outline-danger" block @click="chooseWord('')">Skip my turn</b-button>
    </b-modal>

    <b-modal ref="my-modal-info" size="sm"
      centered hide-footer no-close-on-esc no-close-on-backdrop hide-header>
      <div class="area"><span v-if="count>0">Get ready, {{count}}</span><span v-if="count==0">GO!</span>
      </div>
    </b-modal>

    <b-modal id="my-modal-result" ref="my-modal-result" size="sm"
      centered hide-footer no-close-on-esc no-close-on-backdrop hide-header>
      <span style="color:green;font-size:20px">The winner is</span>
      <jack v-for="u in winners" :key="u" :data-text="u">{{u}}</jack>
      <b-button variant="primary" class="mt-3" block @click="$bvModal.hide('my-modal-result')">Close</b-button>
    </b-modal>

  </div>
</template>
    
<script>
import canvas from "../directives/canvas.directive";
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";
import lineSizeMenu from "@/components/MyLineSizeMenu";
import GameMessageType from "@/services/GameMessageType";
import config from "../myconfig.js";

export default {
  name: "mycanvas",
  components: {
    lineSizeMenu
  },
  // Here we register our custom directive
  directives: {
    canvas
  },

  data() {
    return {
      colorList: [
        "red",
        "orange",
        "green",
        "darkblue",
        "black",
      ],
      strokeStyle: "black",
      background: "Cornsilk",

      name: "jack",
      connected: false,
      sessionID: "",
      stompClient: null,
      socket: null,
      lineSize: 5,

      gameStatus: {started:false,totalRounds:3,currentRound:0,users:[],drawingUser:"",drawingWord:"",timeRemaining:0,drawingWordMask:"____"},
      sortedUsers: [],
      received_messages: [],
      send_message: "",
      wordList: "",

      count: 0,

      winners: [],

      activeFlag: false,
      lastHeartbeatTS: 0,

      chooseWordTimer: null,
      timeLeftForChooseWord: 0,
      countDownTimer: null,
    };
  },
  watch: {
    /*strokeStyle: function (newVal) {
        this.selectColor(newVal);
    },*/
    lineSize: function(newVal) {
      this.$refs.canvas.lineWidth = (this.strokeStyle === this.background) ? 30 : newVal;
    }
  },
  computed: {
    nameState() {
      return this.name.length >= 3 && this.name.length <= 6 ? true : false;
    },
  },

  mounted() {
    this.strokeStyle = "black";
    this.lineSize = 5;
    this.initCanvas();

    this.lastHeartbeatTS = Date.now();
  },

  beforeMount() {
    window.addEventListener("mousemove", this.resetUserActivityTimeout);
    window.addEventListener("scroll", this.resetUserActivityTimeout);
    window.addEventListener("keydown", this.resetUserActivityTimeout);
    window.addEventListener("resize", this.resetUserActivityTimeout);
    window.addEventListener("touchmove", this.resetUserActivityTimeout);
  },

  beforeDestroy() {
    window.removeEventListener("mousemove", this.resetUserActivityTimeout);
    window.removeEventListener("scroll", this.resetUserActivityTimeout);
    window.removeEventListener("keydown", this.resetUserActivityTimeout);
    window.removeEventListener("resize", this.resetUserActivityTimeout);
    window.removeEventListener("touchmove", this.resetUserActivityTimeout);
  },

  methods: {

    // set activeFlag to true if any activity detected.
    // will be reset to false after heartbeat is sent in sendHeartBeat()
    resetUserActivityTimeout() {
      if (!this.activeFlag) this.activeFlag = true;

      // send heart beat message right away if user ghost status is true
      // note: track lastHeartbeatTS to avoid sending too many heartbeat when ghost is true
      let user = this.gameStatus.users.find(x => x.name == this.name);
      if (user && user.ghost && (Date.now() - this.lastHeartbeatTS) > 20000) {
        this.lastHeartbeatTS = Date.now();
        this.sendHeartBeatMsg();
      }
    },

    canvasDisabled() {
      return (this.gameStatus.started && this.gameStatus.drawingUser != this.name);
    },

    sendMsg(ev) {
      if(ev.key === 'Enter') {
        if (this.send_message.length > 0) {
          //console.log("Send message:" + this.send_message);
          if (this.stompClient && this.stompClient.connected) {
            const msg = { type: GameMessageType.USER_GUESS_WORD_REQ, name: this.name, message: this.send_message };
            this.stompClient.send("/app/gameStatus", JSON.stringify(msg), {});
            this.send_message = "";
          }
        }               
      }
    },

    getUserClass(u) {
      let retClass = (u.name == this.gameStatus.drawingUser && this.gameStatus.started) ? 'mr-auto blinking' : 'mr-auto';
      if (u.ghost) {
        retClass = retClass + " linethrough";
      }
      return retClass;
    },

    getColorStyle(x) {
      let style = "height: 30px; border-width: 2px;";
      let extStyle = (x === this.strokeStyle && this.connected)
        ? 'border-style:solid;background:' + x
        : 'background:' + x;
      return style + extStyle;
    },

    async clearCanvas() {

      let retValue = await this.$bvModal.msgBoxConfirm('Please confirm that you want to clear everything.', {
        title: 'Please Confirm',
        okVariant: 'danger',
        okTitle: 'Yes',
        cancelTitle: 'No',
        centered: true
      });
      if (!retValue) return;

      // send request to clear the canvas
      if (this.stompClient && this.stompClient.connected) {
        const msg = { name: this.name, clear: true };
        this.stompClient.send("/app/gameCanvas", JSON.stringify(msg), {});
      }
    },

    initCanvas() {
      this.$refs.canvas.myname = "JACK";

      this.$refs.canvas.connected = this.connected;
  
     // set initial stoke color
      this.$refs.canvas.USER_STROKE = this.strokeStyle;
      this.$refs.canvas.lineWidth = (this.strokeStyle === this.background) ? 30 : this.lineSize;

      //this.$refs.canvas.style = "cursor: url('eraser.png'),pointer;";
      this.$refs.canvas.style.background = this.background;
      this.$refs.canvas.style.border = "1px solid blue";
      this.$refs.canvas.width = 540;

      // call canvas method
      this.$refs.canvas.sayHello();

      // to make canvas call from inside
      this.$refs.canvas.parent = this;

    },

    selectColor(color) {
      if (!this.connected) return;

      this.strokeStyle = color;
      this.$refs.canvas.USER_STROKE = this.strokeStyle;
      if (this.strokeStyle === this.background) {
        // eraser
        this.$refs.canvas.style.cursor = "url('eraser.png'),pointer";
        this.$refs.canvas.lineWidth = 30;
      } else {
        this.$refs.canvas.style.cursor = "pointer";
        this.$refs.canvas.lineWidth = this.lineSize;
      }

      //style="cursor: url('eraser.png'), pointer;"
      console.log(color);
    },

    sayHello() {
      console.log("Hello Jack!");
    },

    // this method is called from inside canvas
    send(lines) {
      if (lines.length > 0) {
        console.log("Send lines:" + lines);
        if (this.stompClient && this.stompClient.connected) {
          const msg = { name: this.name, lines: lines };
          this.stompClient.send("/app/gameCanvas", JSON.stringify(msg), {});
        }
      }
    },
    connect() {

      this.showModal = true;

      this.socket = new SockJS(config.websocketURL);
      this.stompClient = Stomp.over(this.socket);
      this.stompClient.connect(
        {},
        (frame) => {
          console.log(frame);
          
          this.stompClient.subscribe("/topic/gameCanvas", this.processCanvasMessage);

          this.stompClient.subscribe("/topic/gameStatus", this.processGameStatusMessage);

          // send join request
          this.stompClient.send(
            "/app/gameStatus",
            JSON.stringify({ type: GameMessageType.USER_LOGIN_REQ, name: this.name }),
            {}
          );

        },
        (error) => {
          console.log(error);
          this.connected = false;
          this.initCanvas();
        }
      );
    },
    disconnect() {
      if (this.stompClient) {
        this.stompClient.disconnect();
      }
      this.connected = false;
      this.initCanvas();
    },

    startHeartBeatTimer() {

      if (!this.connected || !this.stompClient) return;

      if (this.activeFlag) { // send heartbeat only if it's still active (any mouse/key events)
        this.sendHeartBeatMsg();
        this.activeFlag = false;
      }

      setTimeout(this.startHeartBeatTimer, 30000);
    },

    sendHeartBeatMsg() {
      if (this.connected && this.stompClient) {
        this.stompClient.send(
          "/app/gameStatus",
          JSON.stringify({ type: GameMessageType.USER_SERVER_HEARTBEAT, name: this.name }),
          {}
        );
      }
    },

    disableStart() {
      let realActiveUsers = this.gameStatus.users.filter(u => !u.ghost);
      if (!this.connected || this.gameStatus.started || realActiveUsers.length < 2)
        return true;
      else
        return false;
    },

    startGame() {

      // send start game request
      this.stompClient.send(
        "/app/gameStatus",
        JSON.stringify({ type: GameMessageType.USER_START_GAME_REQ, name: this.name }),
        {}
      );
    },

    chooseWord(word) {
      if (this.chooseWordTimer) clearTimeout(this.chooseWordTimer);

      this.$refs['my-modal'].hide();

      // send choose word request
      this.stompClient.send(
        "/app/gameStatus",
        JSON.stringify({ type: GameMessageType.USER_CHOOSE_WORD_REQ, name: this.name, wordChosen: word}),
        {}
      ); 

    },


    processCanvasMessage(msg) {
      let allLines = JSON.parse(msg.body).lines;
      let isClear = JSON.parse(msg.body).clear;

      if (isClear) { // clear request
        this.$refs.canvas.clear();
        return;
      }

      this.$refs.canvas.paintLines(allLines);
    },

    processGameStatusMessage(msg) {
      //console.log("GameStatus: " + msg.body);

      let msgType = JSON.parse(msg.body).type;
      let userName = JSON.parse(msg.body).name;
      let simSessionID = JSON.parse(msg.body).sessionID;
      let message = JSON.parse(msg.body).message;

      if (message) this.received_messages.push(message);

      this.gameStatus = JSON.parse(msg.body).gameStatus;
      let drawingUser = this.gameStatus.drawingUser;

      this.sortedUsers = this.gameStatus.users.sort((a,b) => b.score - a.score);

      if (msgType == GameMessageType.USER_LOGIN_RESP) {
          if (!this.connected) {
            if (userName) {
              this.sessionID = simSessionID;
              this.connected = true;
              this.initCanvas();

              // subscrible to session-specific messages
              this.stompClient.subscribe("/topic/gameStatus/" + this.sessionID, this.processGameStatusMessage);

              // start heartbeat every 20 seconds
              this.startHeartBeatTimer();

            } else {
              // error
              this.disconnect();
            }
          }

          console.log("user " + userName + ", session is: " + simSessionID);
          return;

      } else if (msgType == GameMessageType.USER_START_GAME_RESP) {
        
        if (this.sessionID == simSessionID) {
          this.sendWorldListReq();
        }
      } else if (msgType == GameMessageType.SERVER_NEXT_USER_REQ) {
        
        if (this.name == drawingUser) {
          this.waitFor3Seconds().then(this.sendWorldListReq);
        } else {
          this.$refs['my-modal'].hide();
          if (this.countDownTimer) clearTimeout(this.countDownTimer);
        }

      } else if (msgType == GameMessageType.USER_WORD_LIST_RESP) {

        this.wordList = JSON.parse(msg.body).wordList;
        this.$refs['my-modal'].show();

        // default to skip if no selection in 15 seconds
        this.chooseWordTimer = setTimeout(() => {
          if (this.countDownTimer) clearTimeout(this.countDownTimer);
          this.chooseWord(""); 
        }, 15000);

        // counting down
        this.timeLeftForChooseWord = 16;
        let countDown = () => {
          this.timeLeftForChooseWord--;
          if (this.countDownTimer) clearTimeout(this.countDownTimer);
          this.countDownTimer = setTimeout(countDown, 1000);
        }

        countDown();

      } else if (msgType == GameMessageType.USER_CHOOSE_WORD_RESP) {
        console.log("USER_CHOOSE_WORD_RESP")

      } else if (msgType == GameMessageType.SERVER_GAME_OVER) {

         this.$refs['my-modal'].hide();

        if (userName && userName.length > 0) {
          this.winners = userName.split(",");

          this.$refs['my-modal-result'].show();
        }
      }

    },

    sendWorldListReq() {
      // send choose word req
      this.stompClient.send(
        "/app/gameStatus",
        JSON.stringify({ type: GameMessageType.USER_WORD_LIST_REQ, name: this.name }),
        {}
      );
    },

    async waitFor3Seconds() {

      const delay = ms => new Promise(res => setTimeout(res, ms));

      // wait for 3 seconds
      this.count = 3;
      this.$refs['my-modal-info'].show();
      while (this.count >= 0) {
        await delay(1000);
        this.count--;

        if (this.count < 0) {
          this.$refs['my-modal-info'].hide();
        }
      }
    }    
  },

};
</script>
<style>
.main {
  justify-content: center;
  height: 100%;
}
.color-guide {
  margin: 10px 10px;
}
h5 {
  margin-bottom: 10px;
}
.color {
  padding: 7px 15px;
  border-radius: 4px;
  margin: 10px 0;
}
  .chatroom {
    min-height: 90%;
    max-height: 90%;
    height: 200px;
    max-width: 230px;
    width: 250px;
    border: 1px solid black;
    padding: 0px 1px;
    overflow-y: auto;
    margin-right: 0px;
    margin-left: 0px;
    margin-bottom: 2px;
  }
  .message {
    list-style: none;
  }

.blinking{
    font-family:cursive;
    animation:blinkingText 1.2s infinite;
}
@keyframes blinkingText{
    0%{     color: rgb(204, 25, 25);    }
    49%{    color: rgb(130, 37, 218); }
    60%{    color: transparent; }
    99%{    color:transparent;  }
    100%{   color: rgb(11, 190, 50);    }
}

.linethrough {
  text-decoration: line-through;
}


.area {
  text-align: center;
  font-size: 36px;
  color: rgb(33, 131, 9);
  letter-spacing: -1px;
  font-weight: 1000;
  text-transform: uppercase;
  animation: blur 1.0s ease-out infinite;
  text-shadow: 0px 0px 5px #fff, 0px 0px 7px #fff;
}

@keyframes blur {
  from {
    text-shadow:0px 0px 10px #fff,
      0px 0px 10px #fff, 
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 50px #fff,
      0px 0px 50px #fff,
      0px 0px 50px #7B96B8,
      0px 0px 150px #7B96B8,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px -10px 100px #7B96B8,
      0px -10px 100px #7B96B8;
  }
}

.blinkingOnce{
    font-family:cursive;
    animation:blinkingText 2s ease-in;
}



jack{
  font-family: 'Teko', sans-serif;
  font-size: 36px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  margin: 0;
  position: relative;
  background: transparent;
  color: #000;
}

jack:before{
  content: attr(data-text);
  position: absolute;
  background: linear-gradient(#f70000, #f89200, #f8f501, #038f00,#0168f8, #a200f7);
  -webkit-background-clip: text;
  color: transparent;
  background-size: 100% 90%;
  line-height: 0.9;
  clip-path: ellipse(30px 30px at -2.54% -9.25%);
  animation: swing 5s infinite;
  animation-direction: alternate;
}

@keyframes swing{
  0%{
     -webkit-clip-path: ellipse(30px 30px at -2.54% -9.25%);
    clip-path: ellipse(30px 30px at -2.54% -9.25%)
  }
  50%{
    -webkit-clip-path: ellipse(30px 30px at 49.66% 64.36%);
    clip-path: ellipse(30px 30px at 49.66% 64.36%);

  }
  100%{
     -webkit-clip-path: ellipse(30px 30px at 102.62% -1.61%);
    clip-path: ellipse(30px 30px at 102.62% -1.61%);
  }
}


</style>
