// canvas.directive.js

function inserted(el) {
  const canvas = el;
  const ctx = canvas.getContext('2d');

  canvas.width = 800;
  canvas.height = 400;
  //canvas.style.cursor = "pointer";

  ctx.lineJoin = 'round';
  ctx.lineCap = 'round';
  //ctx.lineWidth = 5;

  let prevPos = { x: 0, y: 0 };
  let line = [];
  let isPainting = false;
  let isTouch = false;

  canvas.sayHello = function () {
    console.log(canvas.myname);
    console.log(canvas.USER_STROKE);
    console.log(canvas.connected);
    console.log(canvas.background);
  }

  canvas.clear = function () {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
  }

  canvas.paintLines = function (allLines) {
    canvas.parent.sayHello();

    allLines.forEach(pos => {
      //console.log(pos);

      const { x: offsetX, y: offsetY } = pos.stop;
      const { x, y } = pos.start;
      ctx.beginPath();
      ctx.strokeStyle = pos.color;
      ctx.lineWidth = pos.width;
      ctx.moveTo(x, y);
      ctx.lineTo(offsetX, offsetY);
      ctx.stroke();

    });
  }

  function handleMouseDown(e) {
    //alert(isTouch + "=" + e.clientX + "=" + e.clientY);
    //alert(e.offsetX + " and " + e.offsetY);

    if (!canvas.connected) return;

    if (canvas.parent.canvasDisabled()) return;
    
    let { offsetX, offsetY } = e;
    if (isTouch) {
      offsetX = e.clientX;
      offsetY = e.clientY;
      isTouch = false;
    }

    isPainting = true;
    prevPos = { x: offsetX, y: offsetY };
  }
  function endPaintEvent() {
    if (canvas.parent.canvasDisabled()) return;
    
    if (isPainting) {
      isPainting = false;
      sendPaintData();
    }
  }
  function handleMouseMove(e) {
    if (canvas.parent.canvasDisabled()) return;

    if (isPainting) {
      let { offsetX, offsetY } = e;
      if (isTouch) {
        offsetX = e.clientX;
        offsetY = e.clientY;
        isTouch = false;
      }
  
      const offSetData = { x: offsetX, y: offsetY };
      const positionInfo = {
        start: { ...prevPos },
        stop: { ...offSetData },
        color: canvas.USER_STROKE,
        width: canvas.lineWidth
      };
      line = line.concat(positionInfo);
      paint(positionInfo);
    }
  }
  function sendPaintData() {
    canvas.parent.send(line);
    line = [];
  }

  function paint(posInfo) {
    ctx.lineWidth = canvas.lineWidth;

    const { x: offsetX, y: offsetY } = posInfo.stop;
    const { x, y } = posInfo.start;
    ctx.beginPath();
    ctx.strokeStyle = posInfo.color;
    ctx.moveTo(x, y);
    ctx.lineTo(offsetX, offsetY);
    ctx.stroke();

    prevPos = { x: offsetX, y: offsetY };
  }

  canvas.addEventListener('mousedown', handleMouseDown);
  canvas.addEventListener('mousemove', handleMouseMove);
  canvas.addEventListener('mouseup', endPaintEvent);
  canvas.addEventListener('mouseleave', endPaintEvent);

  // for mobile devices ---------------------------------
  // Set up touch events for mobile, etc
  //canvas.addEventListener("touchend", endPaintEvent);
  const evtMap = { touchstart: "mousedown", touchmove: "mousemove", touchend: "mouseup", touchcancel: "mouseup" };
  const evtList = ["touchstart", "touchmove", "touchend", "touchcancel"];
  evtList.forEach(evt => {
    canvas.addEventListener(evt, function (e) {
      if (e.target == canvas) {
        let mousePos = getTouchPos(canvas, e);
        isTouch = true;

        var mouseEvent = new MouseEvent(evtMap[evt], {clientX: mousePos.offsetX, clientY: mousePos.offsetY});
        //alert(mouseEvent.clientX + "XX" + mouseEvent.clientY)
        canvas.dispatchEvent(mouseEvent);

        e.preventDefault();
      }
    }, false);
  });

  // Get the position of a touch relative to the canvas
  
  function getTouchPos(canvasDom, touchEvent) {
    var rect = canvasDom.getBoundingClientRect();

    // for touchEvent.type == touchend or touchcancel
    if (touchEvent.touches.length > 0) {
      return {
        offsetX: touchEvent.touches[0].clientX - rect.left,
        offsetY: touchEvent.touches[0].clientY - rect.top
      };
    }

    return {offsetX: 0, offsetY: 0};
  }
  //---------------------------------------------------

}

export default {
  inserted,
};
